<template lang="pug">
    div.create-conten
        div.edit-logo
            div.add-to
                vue-core-image-upload.upload-img(
                    class="btn btn-primary"
                    @imageuploading="imageuploading"
                    @imageuploaded="imageuploaded"
                    :max-file-size="5242880"
                    :url="doUploadUrl"
                    text="选择图片"
                    inputOfFile="head_image"
                    extensions="png,jpg,gif"
                )
                    img(v-if="head_image" v-lazy="head_image")
                    i.icon.iconfont(v-else) &#xe63e;
            p.explain 编辑公会头像
        div.edit-name
            input(placeholder="请输入公会的名称" v-model="sociaty_name")
        div.edit-manifesto
            textarea(placeholder="请输入公会宣言" v-model="sociaty_manifesto")
        div.btn-group
            div.btn-confirm(@click="submit()") 确认

        //- div.avator-info(
        //-         v-show="isShowAvatorInfo"
        //-     )
        //-         div.avator-info-inner
        //-             div.inner-top
        //-                 span.close(@click="showAvatorInfo") <
        //-                 img.avator-big(v-if="head_image" :src="head_image")
        //-                 img.avator-big(v-else)
        //-             div.inner-bottom
        //-                 vue-core-image-upload.upload-img(
        //-                     class="btn btn-primary"
        //-                     @imageuploading="imageuploading"
        //-                     @imageuploaded="imageuploaded"
        //-                     :max-file-size="5242880"
        //-                     :url="doUploadUrl"
        //-                     text="选择图片"
        //-                     inputOfFile="head_image"
        //-                     extensions="png,jpg,gif"
        //-                 )
        //-                 span.span-close(@click="showAvatorInfo") X


</template>
<script>
import VueCoreImageUpload from 'vue-core-image-upload'
import queryString from 'querystring'
import { Toast, Indicator } from 'mint-ui'
export default {
    name: 'CreateSociety',
    components: {
        'vue-core-image-upload': VueCoreImageUpload
    },
    data() {
        return {
            cropBtn: { ok: '确定', 'cancel': '取消' },
            sociaty_name: '',
            sociaty_manifesto: '',
            head_image: ''
        }
    },
    methods: {
        //- -判断是否上传头像和输入框是否有值
        check() {
            if (!this.head_image) {
                Toast('请为公会添加图片');
                return false;
            }
            else if (!$.trim(this.sociaty_name)) {
                Toast('请输入公会名称');
                return false;
            }
            else if (!$.trim(this.sociaty_manifesto)) {
                Toast('请输入公会宣言');
                return false;
            }
            else {
                return true;
            }
        },
        //- -提交公会信息
        submit() {
            if (this.check()) {
                let axiosUrl = this.api.create_society_app();
                this.axios.post(axiosUrl, queryString.stringify({
                    name: this.sociaty_name,
                    manifesto: this.sociaty_manifesto,
                    logo: this.head_image

                })).then(res => {
                    if (res.data.status == 1) {
                        Toast('创建公会成功');
                        this.$router.push({ path: '/user/index' })
                    }
                    else {
                        Toast(res.data.error || '创建公会失败');
                    }
                })
                    .catch(err => {
                        Toast(err || '操作失败');
                    })
            }
        },
        imageuploading() {
            // 图片上传过程中
            Indicator.open('图片上传中...');
        },
        //- -上传图片
        imageuploaded(result) {
            // 图片上传完
            Indicator.close();
            if (result.state == 200) {
                this.head_image = result.fullname;
            }
            else {
                Toast(result.message || '操作失败');
            }
        },
    },
    //- -上传图片地址
    computed: {
        doUploadUrl() {
            return this.api.doUpload();
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.create-conten {
    background-color: #ededed;
    .edit-logo {
        padding: 1.7rem 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        .add-to {
            height: 4.6rem;
            width: 4.6rem;
            border-radius: 2.5rem;
            background-color: #fff;
            text-align: center;
            line-height: 4.6rem;
            .upload-img {
                height: 100%;
                width: 100%;
                background-color: #fff;
                border-radius: 2.5rem;
                .iconfont {
                    font-size: 1.25rem;
                    color: #bbb;
                }
                img {
                    height: 100%;
                    width: 100%;
                    border-radius: 2.5rem;
                }
            }
            
        }
        .explain {
            margin-top: .5rem;
            color: @fc-dark;
            font-size: .8rem;
        }
    }
    .edit-name {
        width: 100%;
        padding: .5rem .5rem 1rem;
        background-color: #fff;
        input {
            width: 100%;
            height: 100%;
            outline: none;
            border: 0;
        }
    }
    .edit-manifesto {
        width: 100%;
        height: 6.6rem;
        padding: .5rem .5rem 0;
        margin-top: .5rem;
        background-color: #fff;
        textarea {
            height: 100%;
            width: 100%;
            outline: none;
            border: 0;
            resize: none;
        }
    }
    .btn-group {
        margin-top: 1.25rem;
        padding: 0 .5rem;
        .btn-confirm {
            height: 2rem;
            text-align: center;
            line-height: 2rem;
            background-color: @color-theme;
            color: #fff;
            font-size: .75rem;
            border-radius: 1rem;
        }
    }
}

.avator-info {
    .inner-top {
        position: relative;
        width: 100vw;
        height: 100vw;
        .avator-big {
            width: 100%;
            height: 100%;
        }
        .close {
            position: absolute;
            top: .5rem;
            left: .5rem;
            width: 2.5rem;
            height: 2.5rem;
            line-height: 2.5rem;
            text-align: center;
            display: block;
            border-radius: 2.5rem;
            background-color: rgba(0, 0, 0, .5);
            color: #fff;
            font-family: serif;
            font-size: 1.2rem;
        }
    }
}

